@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  border: 0;
  background: none;
  font-family: "Roboto Condensed", sans-serif;
  outline: none;
}

.header {
  text-align: center;
  box-shadow: 0px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;
  padding-bottom: 10px;
}

.cards-list-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 80vw;
  margin: 0 auto;
  padding-top: 20px;
}

.cards-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.only-liked-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(5, 114, 206);
  color: #fff;
  font-weight: 600;
  font-size: 22px;
  border-radius: 4px;
  margin: 20px;
  height: 50px;
  width: 230px;
  transition: 0.25s;
}

.only-liked-button:hover {
  box-shadow: 0 0.5em 0.5em -0.4em #ffa260;
  transform: translateY(-0.25em);
}

.only-liked-icon {
  width: 30px;
  height: 30px;
  margin-left: 10px;
}

.card {
  width: 350px;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 20px;
  font-weight: 600;
}

.card-remove-btn {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 30px;
  height: 30px;
  transition: 0.2s;
}

.card-remove-btn:hover {
  transform: scale(1.2);
}

.card-remove-btn-icon {
  height: 30px;
  width: 30px;
}

.card-img {
  height: 350px;
  width: 350px;
}

.card-like-btn {
  transition: 0.2s;
}

.card-like-btn:hover {
  transform: scale(1.2);
}

.card-like-btn-icon {
  height: 30px;
  width: 30px;
}

.card-bottom {
  padding: 19px;
  margin-bottom: 20px;
  box-shadow: 0 3px 6px rgb(0 0 0 / 16%), 0 3px 6px rgb(0 0 0 / 23%);
  border-radius: 2px;
  border: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

